import React, { PureComponent } from 'react';
import { css } from 'glamor';

import Globals from '../utils/Globals';
import imgAugusta from '../media/images/theater-augusta.png';
import Text from './Text';

const styles = {
  container: css({
    alignSelf: 'flex-end',
    color: Globals.colors.white,
    background: Globals.colors.base,
    width: '100vw',
    display: 'flex',
    '> img': {
      maxWidth: '100%',
    },
    '@media(max-width: 720px)': {
      alignSelf: 'auto',
    },
  }),
  mediumBtn: css({
    color: Globals.colors.background,
    fontSize: 16,
    textAlign: 'center',
    maxWidth: 360,
    borderRadius: 20,
    padding: '10px 20px',
    backgroundColor: Globals.colors.white,
    display: 'block',
    margin: '0 auto',
    textDecoration: 'none',
  }),
};

class TextVenue extends PureComponent {
  render() {
    return (
      <div {...styles.container}>
        <Text title="Local" subtitle="Teatro Augusta, So Paulo, Brasil">
          <p>{Globals.event.locale}</p>
          <p>
            <br />
            No corao de So Paulo, o histrico Teatro Augusta ser o palco de
            apresentaes nicas.
          </p>
          <img src={imgAugusta} alt="Teatro Augusta" title="Teatro Augusta" />
          <small>Foto: Divulgao</small>
          <p>Rua Augusta, 943 - Cerqueira Csar, So Paulo / SP - Brasil</p>
          <p>
            <a
              {...styles.mediumBtn}
              href="https://medium.com/reactbrasil"
              target="_blank"
              rel="noopener noreferrer"
            >
              Hospedagem e Lazer em So Paulo
            </a>
          </p>
        </Text>
      </div>
    );
  }
}

export default TextVenue;



